.wptb-block-wrapper {
  position: relative;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  color: #4A5568;
  border-radius: 5px;
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  grid-template-areas: "header" "main" "footer";
  background-color: #FFF;
  box-sizing: border-box;
  font-size: 80%;
  border: 1px solid #E2E8F0;
  box-shadow: 4px 4px 11px rgba(0, 0, 0, 0.25);
}
.wptb-block-wrapper .wptb-block-button {
  padding: 0 10px;
  width: fit-content;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.2s ease-out;
  margin: 0 10px;
}
.wptb-block-wrapper .wptb-block-button:active {
  animation: wptb-push 0.2s ease-out;
}
.wptb-block-wrapper .wptb-block-button.primary {
  background-color: #3299D1;
  color: #FFF;
}
.wptb-block-wrapper .wptb-block-button.negative {
  background-color: #F56565;
  color: #FFF;
}
.wptb-block-wrapper .wptb-block-button.wptb-block-button-disabled {
  cursor: default;
  background-color: #CBD5E0 !important;
  pointer-events: none !important;
}
.wptb-block-wrapper div {
  margin: 0;
  padding: 0;
}
.wptb-block-wrapper .wptb-block-header {
  height: 100%;
  grid-area: header;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #E2E8F0;
  overflow: hidden;
}
.wptb-block-wrapper .wptb-block-header .wptb-plugin-left-toolbox {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.wptb-block-wrapper .wptb-block-header .wptb-plugin-left-toolbox .wptb-plugin-brand {
  padding: 5px 10px;
  background-color: #3299D1;
  color: white;
}
.wptb-block-wrapper .wptb-block-header .wptb-plugin-left-toolbox .wptb-plugin-selected-header-info {
  font-style: italic;
  margin-left: 10px;
  color: #3299D1;
}
.wptb-block-wrapper .wptb-header-toolbox {
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.wptb-block-wrapper .wptb-block-main {
  height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-selected-view {
  width: 200px;
  height: 100%;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-preview {
  border: 1px solid #E2E8F0;
  border-radius: 5px;
  background-color: #E2E8F0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-preview .no-table-selected-preview {
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  color: #CBD5E0;
  font-size: 400%;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-select {
  width: 100%;
  height: 100%;
  display: grid;
  grid-gap: 60px;
  grid-template-columns: 1fr 200px;
  grid-template-rows: 1fr;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-select .wptb-table-list-wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-select .wptb-table-list-wrapper .wptb-table-search {
  margin-bottom: 10px;
  position: relative;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-select .wptb-table-list-wrapper .wptb-table-search .wptb-search-clear {
  position: absolute;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  right: 10px;
  color: #CBD5E0;
  font-weight: bold;
  cursor: pointer;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-select .wptb-table-list-wrapper .wptb-table-search input {
  border-radius: 999px !important;
  border: 2px solid #E2E8F0 !important;
  padding: 0 !important;
  text-align: center;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-select .wptb-table-list-wrapper .wptb-table-search input:active, .wptb-block-wrapper .wptb-block-main .wptb-table-select .wptb-table-list-wrapper .wptb-table-search input:focus {
  box-shadow: none !important;
  border: 2px solid #CBD5E0 !important;
  outline: 0 !important;
  inset: 0 !important;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-select .wptb-table-list-wrapper .wptb-table-list {
  height: 100%;
  overflow-y: scroll;
  width: 100%;
  font-size: 90%;
  border: 1px solid #E2E8F0;
  border-radius: 5px;
  padding: 5px;
  position: relative;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-select .wptb-table-list-wrapper .wptb-table-list .wptb-table-list-row {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  justify-content: center;
  align-items: center;
  padding: 3px;
  cursor: pointer;
  transition: all 0.3s;
  opacity: 0;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-select .wptb-table-list-wrapper .wptb-table-list .wptb-table-list-row[data-is-active=true] {
  background-color: #216c95 !important;
  color: #FFF !important;
  cursor: default;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-select .wptb-table-list-wrapper .wptb-table-list .wptb-table-list-row .wptb-table-list-id {
  justify-self: center;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-select .wptb-table-list-wrapper .wptb-table-list .wptb-table-list-row:nth-of-type(odd) {
  background-color: #E2E8F0;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-select .wptb-table-list-wrapper .wptb-table-list .wptb-table-list-row:hover {
  background-color: #CBD5E0;
}
.wptb-block-wrapper .wptb-block-main .wptb-table-select .wptb-table-list-wrapper .wptb-table-list .selected {
  background-color: #3299D1 !important;
  color: #FFF;
}
.wptb-block-wrapper .wptb-block-footer {
  border-top: 1px solid #E2E8F0;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.wptb-block-wrapper .wptb-block-footer .wptb-block-footer-right {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}
.wptb-block-wrapper .wptb-block-toggle:after {
  content: "\f347";
}
.wptb-block-wrapper .wptb-block-toggle[aria-expanded=true]:after {
  content: "\f343";
}

.wptb-basic-appear-anim {
  animation: wptb-basic-appear 0.3s ease-out forwards;
}

.wptb-block-search-indicator {
  font-weight: bold;
  color: #48BB78 !important;
}

.wptb-block-tool {
  height: 100%;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-left: 1px solid #E2E8F0;
  cursor: pointer;
  transition: all 0.2s ease-out;
}
.wptb-block-tool[data-disabled=true] {
  color: #CBD5E0 !important;
}
.wptb-block-tool[data-disabled=true]:hover {
  background-color: inherit !important;
}
.wptb-block-tool:hover {
  background-color: #E2E8F0 !important;
}

.wptb-block-full-preview {
  position: relative;
  display: flex;
  justify-content: center;
}
.wptb-block-full-preview .wptb-block-minimize-button {
  position: absolute;
  height: 40px;
  top: -20px;
  right: -20px;
  background-color: #FFF;
  z-index: 10000;
}

.wptb-block-shadow {
  box-shadow: 4px 4px 11px rgba(0, 0, 0, 0.25);
}

.wptb-block-builder-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: #FFF;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: "main";
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease-out;
}
.wptb-block-builder-wrapper.hide {
  top: -100%;
  transform: scale(0.5);
}
.wptb-block-builder-wrapper .wptb-block-builder {
  box-sizing: border-box;
  margin: 0;
  grid-area: main;
  width: 100%;
  height: 100%;
  border: 5px solid #CBD5E0;
  z-index: 20;
}
.wptb-block-builder-wrapper .wptb-block-builder-load-indicator {
  grid-area: main;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #3299D1;
  background-color: #FFF;
  z-index: 10;
  position: relative;
}

.wptb-block-overlay {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.7);
  color: #3299D1;
  z-index: 100000000;
}
.wptb-block-overlay .wptb-busy-overlay {
  animation: wptb-rotate 1s linear infinite;
}

.wptb-block-info-label {
  text-transform: capitalize;
}

.wptb-block-info-value {
  font-style: italic;
}

@keyframes wptb-basic-appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes wptb-push {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes wptb-rotate {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(-360deg);
  }
}